.movieListItem {
  border-radius: 5px;
  background-color: whitesmoke;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2), 1px 1px 2px rgba(0, 0, 0, 0.2),
    1px 1px 2px rgba(0, 0, 0, 0.2);
}
.link {
  text-decoration: none;
  color: black;
}

.fosterImage {
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  width: 250px;
  height: 300px;
}
.movieListItem:hover {
  transform: scale(1.1);
}
.movieTitle {
  text-decoration: none;
  padding: 20px;
  font-size: 16px;
  font-weight: 700;
  text-align: center;
  width: 200px;
}
